  background-color: #4e86a6;
  border: none;
  outline: none;
}
@media (min-width: 768px), (min-width: 1200px) {
  .main-header__link:focus-visible {
    background-color: rgba(0, 0, 0, 0);
    outline: 3px solid #5496bd;
    outline-offset: -3px;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.modal.is-open {
  display: flex;
}

.modal-content {
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
}
.modal-content--failure {
  display: none;
  max-width: 400px;
  padding: 45px 42px 35px 41px;
}
@media (min-width: 768px) {
  .modal-content--failure {
    padding: 50px 47px 49px 46px;
  }
}
.modal-content--success {
  z-index: -1;
  display: block;
  max-width: 640px;
  padding: 39px 20px 20px 20px;
}
.modal-content--success .modal-content__text {
  margin-bottom: 50px;
  padding: 0 12px;
}
@media (min-width: 768px) {
  .modal-content--success .modal-content__text {
    margin-bottom: 74px;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .modal-content--success .modal-content__text {
    margin-bottom: 72px;
  }
}
@media (min-width: 768px) {
  .modal-content--success {
    padding: 53px 91px 44px 91px;
  }
}

.modal-content__title {
  margin: 0 0 37px 0;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal-content__title {
    font-size: 30px;
    line-height: 40px;
  }
}

.modal-content__text {
  margin: 0 0 28px 0;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
}